import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IconType } from '@core/enums';
import { PropertyAlertsFacade } from '@feature/property-alerts/property-alerts.facade';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-property-alerts-dialog',
    templateUrl: './property-alerts-dialog.component.html',
    styleUrls: ['./property-alerts-dialog.component.scss'],
    providers: [PropertyAlertsFacade],
    standalone: false
})
export class PropertyAlertsDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data,
		public dialogRef: MatDialogRef<PropertyAlertsDialogComponent>,
		private propertAlert: PropertyAlertsFacade
	) {}

	readonly iconType = IconType;
	readonly buttonType = ButtonType;

	onCloseDialog(): void {
		this.dialogRef.close();
	}

	deleteAlert(): void {
		this.propertAlert.deleteAlert(this.data.id).subscribe({
			next: () => this.dialogRef.close({ status: true, alertId: this.data.id }),
			error: () => this.dialogRef.close({ status: false }),
		});
	}
}
