import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ButtonType } from '@shared/components/button/button.component';
import { PropertyAlertsDialogComponent } from './components/property-alerts-dialog/property-alerts-dialog.component';
import { PropertyAlertsFacade } from './property-alerts.facade';
import { SnackService } from '@core/services/snack.service';
import { ViewportService } from '@core/services/viewport.service';

@Component({
    selector: 'findqo-property-alerts',
    templateUrl: './property-alerts.component.html',
    styleUrl: './property-alerts.component.scss',
    standalone: false
})
export class PropertyAlertsComponent implements OnInit {
	readonly buttonType = ButtonType;

	alertsList = [];
	alertsMetaData;
	isLoaded = false;

	constructor(
		private dialog: MatDialog,
		private propertAlert: PropertyAlertsFacade,
		private snackService: SnackService,
		public viewport: ViewportService
	) {}

	onRemoveAlerts(elem) {
		this.dialog
			.open(PropertyAlertsDialogComponent, {
				disableClose: true,
				maxWidth: '407px',
				data: elem,
			})
			.afterClosed()
			.subscribe({
				next: (res) => {
					if (res?.['status']) {
						this.alertsList = this.alertsList.filter((resp) => resp.id !== res.alertId);
						this.snackService.open({
							data: { message: 'Property alert removed successfully.', type: 'success' },
							panelClass: 'snackbar-success',
						});
					}
				},
			});
	}

	getAlertsList(page = 1, perPage = 10) {
		this.propertAlert.getPropertyAlerts({ page, perPage }).subscribe({
			next: (res) => {
				this.isLoaded = true;
				this.alertsList = res['data'];
				this.alertsMetaData = res['meta'];
			},
			error: () => {
				this.isLoaded = false;
				this.snackService.open({
					data: {
						message: 'Something went wrong',
						type: 'error',
					},
					panelClass: 'snackbar-error',
				});
			},
		});
	}

	ngOnInit(): void {
		this.getAlertsList();
	}
}
